.current-weather {
  display: -ms-flexbox;
  display: flex;
  position: relative;
  margin-top: 110px;
  padding-bottom: 20px;
}

.weather-img {
  -ms-flex-positive: 3;
      flex-grow: 3;
}

.weather-current {
  -ms-flex-positive: 4;
      flex-grow: 4;
  margin-top: 45px;
}

.weather-city {
  font-size: 22px;
  color: #fff;
  font-weight: 300;
  opacity: 0.8;
}

.weather-info-main {
  font-size: 35px;
  color: #fff;
  font-weight: 600;
}

.weather-temp {
  font-size: 25px;
  color: #fff;
  font-weight: 300;
  opacity: 0.8;
}


